.rows {
    color: white;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
}

.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 20px;
}

.row_poster{
    max-height: 100px;
    object-fit: contain;
    margin-right: 10px;
    width: 100%;
    transition: transform 450ms;
    border-radius: 0.8rem;
}

.row_poster:hover{
    transform: scale(1.08);
    opacity: 1;
}

.row_posterLarge{
    max-height: 250px;
}

.row_poster:hover{
    transform: scale(1.09);
    opacity: 1;
}

.row_posters::-webkit-scrollbar {
    display: none; 
}
